$primary_1: #0b0000;
$myFont: Georgia;
$myBorder: 1px solid;
$myTextAlign: center;
$myFontWeight: bold;
$myTextDeco: none;

body {
  display: center;
  background-image: url(https://st2.depositphotos.com/3591429/5246/i/950/depositphotos_52462601-stock-photo-antivirus-protection-system.jpg);
  background-color: #cccccc; /* Used if the image is unavailable */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
}

.container{
    @extend h1;
    text-decoration: $myTextDeco;
  }
  
h1 {
    color: $primary_1;
    font-family: $myFont;
    font-weight: $myFontWeight;
    text-align: $myTextAlign;
  }
  
h2 {
    @extend h1;
  }

p {
    color: #043849;
    font-family: $myFont;
    text-align: $myTextAlign;
    font-weight: $myFontWeight;
    font-size: 35;
  }
  
.links a{
    @extend h1;
    text-decoration: $myTextDeco;
    font-size: 18;
    display: inline-flex;
    color: rgb(6, 12, 168);
    padding: 25px 15px;
    background-color: white;
    width: 105px;
    height: 60px;
    margin: 10px 10px; 
}

a {
  text-decoration: $myTextDeco;
  font-size: 20;
  color: rgb(38, 11, 146);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 15px 20px;
  width: 65px;
  height: 20px;
  margin: 0px 15px;
}

a:visited {
  color: rgb(77, 20, 92);
  text-decoration: $myTextDeco;
  text-align: $myTextAlign; 
} 

a:hover {
  color: #275092;
  text-decoration: $myTextDeco;
  font-weight: $myFontWeight;
  text-align: $myTextAlign; 
}

a:active {
  color: #75064a;
  text-decoration: $myTextDeco;
  font-weight: $myFontWeight;
  text-align: $myTextAlign;
}

  @media (max-width: 600px) {
    .container {
      display: flexbox;
    }
  }
  
  